<template>
    <div id='box'>
      <div class="top">
        <van-search
          v-model="keyword"
          placeholder="请输入搜索关键词"
          show-action
          @blur="$router.push({path:'/search_detail',query:{keyword}})"
          shape="round"/>
      </div>
      <div>
        <div class="item" v-if="lishi!=1">
          <div class="top">
            <h3>历史搜索</h3>
            <img src="@/assets/images/my/ic_delete.png" alt="" class="img" @click="del()">  <!-- 清除本地缓存 -->
          </div>
          <div class="bottom">
            <span v-for="(item,index) in lishi" :key="index" @click="$router.push({path:'/search_detail',query:{keyword:item}})">{{item}}</span>
          </div>
        </div>

        <div class="item">
          <div class="top">
            <h3>大家都在搜</h3>
          </div>
          <div class="bottom">
            <div v-for="(item,index) in resou" :key="index" @click="$router.push({path:'/search_detail',query:{keyword:item}})">{{item}}</div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { Search, Dialog } from 'vant';
import { getresou } from "@/axios/getData";
export default {
    components: {
      vanSearch: Search
    },
    props: {

    },
    data() {
        return {
          resou:[],    //  热搜列表
          keyword:'',   //  搜索的关键字
          lishi:1
        };
    },
    computed: {

    },
    created() {
      try{
        let arr = new Set(JSON.parse(localStorage.getItem('key')))
        this.lishi =Array.from(arr)
      }catch(err){
        console.log(123)
      }
      this.getdata();
    },
    mounted() {

    },
    methods: {
      //  获取热搜数据
      async getdata(){
        var res = await getresou();
        this.resou = res.data
        console.log(res);
      },
      // 清除缓存
      del(){
        Dialog.alert({
          title: '确定要删除吗？',
          message: "删除不可恢复哦！",
          cancelButtonText:'取消',
          cancelButtonColor:'#15BDDB',
          showCancelButton:true
        }).then(()=>{
          this.lishi = 1
          localStorage.removeItem('key');
        }).catch(()=>{})
      }
    },
};
</script>

<style scoped lang="scss">
#box{
  height:100vh;
  background: #FAFAFA;
}
.item{
  .top{
    padding: 15pt;
    display: flex;
    justify-content: space-between;
    .img{
      width:24pt;
      height:24pt;
    }
    h3{
      line-height: 24pt;
    }
  }
  .bottom{
    padding: 0 15pt;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    span{
      display: inline;
      padding:2pt 6pt;
      border:1pt solid #DEDEDE;
      border-radius: 20pt;
      margin-right:20pt;
      color:#666;
    }
    div{
      width:35%;
      margin-bottom:10pt;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      // text-decoration: underline;
    }
  }
}
</style>



